.form {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-width: 326px;
  // margin-right: 15px;
}

.form label {
  position: absolute;
  top: -1000px;
  left: -1000px;
}

.form input {
  padding-left: 27px;
  font-weight: normal;
  height: 48px;
  border: 1px solid #c9ccd4;
  border-radius: 4px;
  font-size: 15px;
  line-height: 24px;
  margin-bottom: 20px;

  &::placeholder {
    font-weight: normal;
    font-size: 15px;
    color: #1c1c1c;
    opacity: 0.5;
  }

  &:hover {
    border: 1px solid $cyan;
  }

  &:focus {
    background-color: rgba(230, 230, 230, 0.5);
  }
}

.form__title {
  margin: 7px 0 20px;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  text-align: center;
  color: #ffffff;
  letter-spacing: -0.005em;
}

.form button {
  background-color: $cyan;
  height: 51px;
  border: none;
  border-radius: 6px;
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  text-align: center;
  color: #000000;
  cursor: pointer;
}

@media (max-width: 1023px) {
  .form {
    max-width: 42.8%;
    margin: 113px auto 0 auto;
    position: relative;
    left: 0;
    top: 0;
    padding-top: 0;
  }

  .form__title {
    color: #000000;
    font-size: 18px;
    padding-top: 30px;
    margin-bottom: 30px;
  }

  .form input {
    padding-left: 20px;
  }

  .form__wrapper {
    margin-left: -10px;
  }
}

@media (max-width: 767px) {
  .form {
    max-width: 90%;
    padding-top: 0;
    margin: 102px auto 0 auto;
  }

  .form input {
    padding-left: 24px;
  }

  .form__title {
    margin-bottom: 20px;
  }
}
